import { RingVolume } from '@material-ui/icons'
import { useRouterContext } from '@speedlo/hooks'
import React, { useCallback } from 'react'

import { Colors } from '../../../styling'
import { ROUTE_ORDER_CREATE } from '../../order/order.routes'
import {
  DashboardButton,
  IDashboardButtonProps,
} from '../atoms/DashboardButton'

interface IProps extends IDashboardButtonProps {}

export const CallMeDashboardButton: React.FC<IProps> = props => {
  const { history } = useRouterContext()
  const makeCall = useCallback(
    (ev: React.MouseEvent<HTMLButtonElement>) => {
      const phone = window.prompt(
        'Zadej telefonní číslo, které ti bude volat (bez předvolby)',
      )
      if (phone === null) {
        return
      }
      let eventId: MaybeID = null
      if (ev.shiftKey) {
        eventId = window.prompt('A teď ještě eventId, jestli víš co děláš ;)')
      }
      history.push({
        pathname: ROUTE_ORDER_CREATE,
        state: {
          eventId,
          phone,
        },
      })
    },
    [history],
  )

  if (process.env.REACT_APP_STAGE === 'prod') {
    return null
  }

  return (
    <DashboardButton
      {...props}
      text="Zavolej mi [DEV]"
      icon={RingVolume}
      color={Colors.turquoise}
      onClick={makeCall}
    />
  )
}
