import { RingVolume } from '@material-ui/icons'
import { LocationDescriptor } from 'history'
import React from 'react'

import { TouchButtonBadge } from '../../../atoms/TouchButtonBadge'
import { useSOrderCall } from '../../../graph/generated'
import { ROUTE_ORDER_CREATE } from '../order.routes'

export function useOrderCallStatus() {
  const { data } = useSOrderCall()

  const location: LocationDescriptor = {
    pathname: ROUTE_ORDER_CREATE,
    state: data ? data.call : null,
  }

  const badge =
    data && data.call && data.call.isRinging ? (
      <TouchButtonBadge>
        <RingVolume />
      </TouchButtonBadge>
    ) : null

  return { location, badge }
}
